<template>
  <el-container>
    <el-main>
      <el-card class="box-card animated bounceInUp">
        <!-- 左边区域年份 -->
        <div class="document_left">
          <p style="color:red">CateGory</p>
          <h3>{{category}}</h3>
        </div>
        <div class="document_right">
          <el-table
            :data="tableData.filter(data => !search || data.title.toLowerCase().includes(search.toLowerCase()))"
            style="width: 100%"
          >
            <el-table-column label="Date" prop="date"></el-table-column>
            <el-table-column label="Title" prop="title"></el-table-column>
            <el-table-column align="right">
              <template slot="header" slot-scope="scope">
                <el-input v-model="search" size="mini" placeholder="输入关键字搜索"/>
              </template>
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="danger"
                  @click="handleEdit(scope.$index, scope.row)"
                >阅读全文</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-card>
    </el-main>
  </el-container>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      search: "",
      tableData: [],
      category: ""
    };
  },
  mounted() {
    this.getList();
  },
  methods: {
    handleEdit(index, row) {
     this.$router.push({path:'/homen/artical',query:{i:index,row:row}})
    },
    handleDelete(index, row) {
      console.log(index, row);
    },
    getList() {
      axios
        .get("http://lxt.anxin.fun/db.json/artical")
        .then(res => {
          // console.log(res.data)
          this.tableData = res.data;
           this.category = res.data[0].category[0];
          console.log(this.list);
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>
<style lang="scss" scoped>
.box-card {
  width: 100%;
  background: rgba($color: #fff, $alpha: 0.8);
  opacity: 0.85;
  border-radius: 15px;
  overflow: hidden;
}
// <!-- 左边区域年份 -->
.document_left {
  width: 16%;
  height: 100%;
  float: left;
  p{
    font-size: 24px;
  }
}
.document_right {
  float: left;
  width: 84%;
}
</style>




