<script>
import axios from "axios";
export default {
  data() {
    return {
      list: []
    };
  },
  mounted() {
    this.getList();
  },
  methods: {
    open11() {
      this.$notify({
        title: "嘚瑟",
        message: "我会让你去前端？别想了",
        offset: 100
      });
    },
    open12() {
      this.$notify({
        title: "膨胀",
        message: "我会让你去LocalStorage？别想了",
        offset: 100
      });
    },
    open13() {
      this.$notify({
        title: "累吗",
        message: "我会让你去SessionStorage？别点了",
        offset: 100
      });
    },
    getList() {
      this.$ajax
        .get("http://lxt.anxin.fun/db.json/artical")
        .then(res => {
          this.list = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.home_main {
  background: rgba($color: #e6eaee, $alpha: 0.8);
  opacity: 0.85;
}
/* 区域文档样式 */
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
// 文档自定义样式开始
.box-card {
  padding: 0;
  width: 100%;
  background-color: white;
  border-radius: 20px;
  margin-bottom: 40px;
  // 文章标题区域
  .main_title_header {
    display: flex;
    justify-content: space-between;
    text-align: center;
    height: 30px;
    margin-bottom: 10px;
    // background-color: pink;
    .right {
      font-size: 24px;
      font-family: "宋体";
      font-weight: 600;
      vertical-align: middle;
    }
    .left {
      display: flex;
      justify-content: space-between;
      line-height: 36px;
      span {
        display: block;
        font-size: 14px;
      }
      .time_log {
        width: 14px;
        height: 14px;
        // background-color: pink;
        margin-right: 5px;
        vertical-align: middle;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .time_d {
        line-height: 32px;
      }
    }
  }
  // 文章内容区域
  .main_middle_header {
    max-height: 150px;
    overflow: hidden;
  }
  // 最底部链接处
  .main_middle_href {
    display: flex;
    justify-content: space-between;
    //箭头链接区域
    .crrows_box {
      overflow: hidden;
      .main_crrows01:nth-child(1) {
        margin-right: 10px;
      }
      .main_crrows01 {
        // background: pink;
        float: left;
        img {
          display: block;
          height: 25px;
          width: 20px;
          float: left;
        }
        a {
          .triangle_left {
            display: block;
            float: left;
            width: 0;
            height: 0;
            border-top: 12px solid transparent;
            border-right: 15px solid #85aadf;
            border-bottom: 12px solid transparent;
          }
          .triangle_right {
            display: block;
            float: left;
            height: 24px;
            line-height: 22px;
            font-size: 14px;
            color: white;
            background: #85aadf;
            margin-right: 10px;
            padding-right: 5px;
          }
          .bd_color01 {
            border-right: 15px solid #fbd3db;
          }
          .bg_color01 {
            background-color: #fbd3db;
          }
          .bd_color02 {
            border-right: 15px solid #51c8f3;
          }
          .bg_color02 {
            background-color: #51c8f3;
          }
          .bd_color03 {
            border-right: 15px solid #b41fda;
          }
          .bg_color03 {
            background-color: #b41fda;
          }
          .bd_color04 {
            border-right: 15px solid #da1f8c;
          }
          .bg_color04 {
            background-color: #da1f8c;
          }
        }
      }
      .main_crrows01:hover:nth-child(1) {
        a {
          .triangle_left {
            border-right: 15px solid rgb(75, 15, 109);
          }
          .triangle_right {
            background: rgb(75, 15, 109);
          }
        }
      }
      .main_crrows01:hover:nth-child(2) {
        a {
          .triangle_left {
            border-right: 15px solid rgb(247, 52, 17);
          }
          .triangle_right {
            background: rgb(247, 52, 17);
          }
        }
      }
      .main_crrows01:hover:nth-child(3) {
        a {
          .triangle_left {
            border-right: 15px solid rgb(223, 8, 133);
          }
          .triangle_right {
            background: rgb(223, 8, 133);
          }
        }
      }
    }
    // <!-- 阅读全文超链接 -->
    .artical_href {
      height: 24px;
      line-height: 24px;
      border-radius: 5px;
      text-align: center;
      background-color: #50cdfb;
      color: white;
      cursor: pointer;
      font-size: 14px;
      span:nth-child(1) {
        font-family: "宋体";
        padding-left: 5px;
        padding-right: 5px;
        font-weight: 600;
      }
      span:nth-child(2) {
        font-size: 14px;
        padding-right: 5px;
      }
    }
    .artical_href:hover {
      background-color: #da1f8c;
    }
  }
}
</style>




