<template>
  <div class="share_list">
    <h3>友情链接</h3>
    <el-table
      :data="tableData.filter(data => !search || data.blurb.toLowerCase().includes(search.toLowerCase()))"
      style="width: 100%"
    >
      <el-table-column label="Name" prop="name"></el-table-column>
      <el-table-column label="Synopsis" prop="blurb"></el-table-column>
      <el-table-column align="right">
        <template slot="header" slot-scope="scope">
          <el-input v-model="search" size="mini" placeholder="输入关键字搜索"/>
        </template>
        <template slot-scope="scope">
          <a
            size="medium"
            type="danger"
            :href="tableData[scope.$index].address"
          >点击查看</a>
          <el-button
            size="mini"
            style="opacity:0;cursor: Default"
            @click="handleEdit(scope.$index, scope.row)"
          >Edit</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tableData: [],
      search: ""
    };
  },
  mounted() {
    this.getList();
  },
  methods: {
    handleEdit(index, row) {
      console.log(index, row);
    },
    handleDelete(index, row) {
      console.log(index)
      console.log(this.tableData[index])
      this.$router.push({path:this.tableData[index].address})
    },
    getList() {
      this.$ajax.get("http://lxt.anxin.fun/db.json/share").then(res => {
        this.tableData = res.data;
        console.log(res.data);
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.share_list {
  width: 100%;
  background: rgba($color: #fff, $alpha: 0.8);
  opacity: 0.85;
  text-align: center;
  h3 {
    font-size: 30px;
    color: crimson;
  }
}
</style>



