<template>
  <el-container>
    <el-main>
      <el-card class="box-card">
        <!-- 左边区域年份 -->
        <div class="document_left">
          <p>TAGS</p>
          <h3>2018</h3>
        </div>
        <div class="document_right">
          <el-table :data="list" style="width: 100%">
            <el-table-column label="标题">
              <template slot-scope="scope">
                <el-popover trigger="hover" placement="top">
                  <p>标题: {{ scope.row.title}}</p>
                  <p>类别: {{ scope.row.category[0]}}</p>
                  <div slot="reference" class="name-wrapper">
                    <el-tag size="medium">{{ scope.row.title }}</el-tag>
                  </div>
                </el-popover>
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <!-- <router-link  :to="{path:'/homen/artical',query:{i:scope.$index}}"  tag="button" class="click_button">阅读全文</router-link> -->
                <el-button
                  size="mini"
                  type="danger"
                  @click="handleEdit(scope.$index, scope.row)"
                >阅读全文</el-button>
              </template>
            </el-table-column>
            <el-table-column label="日期" width="180">
              <template slot-scope="scope">
                <i class="el-icon-time"></i>
                <span style="margin-left: 10px">{{ scope.row.date }}</span>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-card>
    </el-main>
  </el-container>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      list: [],
      category: ""
    };
  },
  mounted() {
    this.getList();
  },
  methods: {
    handleEdit(index, row) {
      this.$router.push({path:'/homen/artical',query:{i:index,row:row}})
    },
    getList() {
      axios
        .get("http://lxt.anxin.fun/db.json/artical")
        .then(res => {
          this.list = res.data;
          this.category = res.data[0].category[0];
          console.log(this.list);
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>
<style lang="scss" scoped>
.box-card {
  width: 100%;
  background: rgba($color: #fff, $alpha: 0.8);
  opacity: 0.85;
  border-radius: 15px;
}
// <!-- 左边区域年份 -->
.document_left {
  width: 16%;
  height: 100%;
  //   background-color: pink;
  float: left;
  p {
    color: red;
    font-size: 24px;
  }
}
.document_right {
  float: left;
  width: 84%;
}
</style>


